.ProductionInfo {
    background: #EEE;
    -webkit-box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
            box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid #CCC;
    border-radius: 3px;

    position: relative ;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: distribute;
        align-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;

    width: 880px;
    height: 100px;

    margin-bottom: 10px;

}

.ProductionInfo div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center; /* align horizontal */
    -ms-flex-align: center;
        align-items: center;

    width: 300px;
    height: 35%;
    background-color: #EEE;
    -webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
            box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
    border: 1px solid #CCC;
    border-radius: 3px;


}

.ProductionInfo div img {
    margin-left: 5px;
}

.ProductionInfo > div:hover {
    -webkit-box-shadow: 0 4px 8px 0 rgba(30, 136, 229, 0.2), 0 6px 20px 0 rgba(30, 136, 229, 0.19);
            box-shadow: 0 4px 8px 0 rgba(30, 136, 229, 0.2), 0 6px 20px 0 rgba(30, 136, 229, 0.19);
}


